body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "VT323", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
  align-items: center;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100vw;
  height: 177.5vw;
  max-width: 500px;
  max-height: 887.5px;
  color: #ffffd2;
}

