body {
  background: #3A5BA6;
}

.menu-options {
  position: absolute;
  z-index: 10;
  left: 0%;
  width: 100%;
  height: 100%;
  background: #C4E3F8;
  margin: 0;
  padding: 0;
  display: inline-block;
  -webkit-animation: 0.3s agrandar 1;
          animation: 0.3s agrandar 1;
}

@-webkit-keyframes agrandar {
  0% {
    opacity: 0%;
    left: 100%;
  }
}

@keyframes agrandar {
  0% {
    opacity: 0%;
    left: 100%;
  }
}
.GameOver {
  position: absolute;
  z-index: 11;
  width: 100%;
  height: 100%;
  background: #C4E3F8;
  margin: 0;
  padding: 0;
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
  font-size: 2em;
}
.GameOver p {
  padding: 10%;
  text-align: center;
}
.GameOver button {
  width: 100%;
  height: 100%;
  background: #3A5BA6;
  margin: 0;
  padding: 0;
  width: 50%;
  height: auto;
  padding: 5%;
  color: #ffffd2;
  font-size: 0.5em;
  border-style: none;
  text-decoration: none;
}

.botonSuperior {
  display: flex;
  justify-content: flex-end;
}

.boton-menuP {
  background: none;
  border-style: none;
}

.boton-menuP:focus {
  outline-style: none;
}

.opc {
  position: relative;
  margin-left: 2.5em;
}

.abierto {
  height: 6%;
  width: 100%;
  color: #ffffd2;
  font-size: 2em;
  display: flex;
  align-items: flex-start;
}

.Criatura {
  position: relative;
  width: 100%;
  height: 100%;
  background: #C4E3F8;
  margin: 0;
  padding: 0;
  height: 100vh;
  overflow: hidden;
  color: #3A5BA6;
  max-width: 100%;
  min-height: 100%;
  display: flex;
  flex-flow: column wrap;
  align-content: stretch;
}
.Criatura header {
  width: 100%;
  height: 8%;
  background: #3A5BA6;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
}
.Criatura header div {
  position: relative;
  display: flex;
  justify-content: center;
  font-size: 2em;
}
.Criatura header div p {
  align-self: center;
  display: flex;
  flex-flow: column no-wrap;
  color: #ccc;
  font-size: 1em;
}
.Criatura h1 {
  width: 100%;
  height: 4%;
  background: #3A5BA6;
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: 1.5em;
  color: #ccc;
}
.Criatura h1 input {
  width: 100%;
  height: 100%;
  background: none;
  margin: 0;
  padding: 0;
  color: #ccc;
  border-style: none;
  text-align: center;
  font-size: 1em;
}
.Criatura h1 input:focus {
  outline-style: none;
}
.Criatura fondo {
  width: 100%;
  height: 55%;
  background: none;
  margin: 0;
  padding: 0;
  background-image: url(/petops/static/media/base0.bd9705a1.svg);
  background-repeat: no-repeat;
  background-size: 100%;
  display: block;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
.Criatura h2 {
  width: 100%;
  height: 6%;
  background: #3A5BA6;
  margin: 0;
  padding: 0;
  color: #C4E3F8;
  text-align: center;
  font-size: 1.5em;
  padding-top: 0.3em;
  padding-bottom: 1%;
}
.Criatura footer {
  width: 100%;
  height: 20%;
  background: #3A5BA6;
  margin: 0;
  padding: 0;
}
.Criatura footer p {
  text-align: center;
  font-size: 1.5em;
  color: #fcbad3;
}
.Criatura footer div {
  width: 100%;
  height: 100%;
  background: none;
  margin: 0;
  padding: 0;
  border-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Criatura footer div .botonAccion {
  width: 100%;
  height: 100%;
  background: none;
  margin: 0;
  padding: 0;
  width: 22%;
  height: auto;
  border-style: none;
  padding: 2%;
  cursor: pointer;
}
.Criatura footer div .botonAccion:focus {
  outline-style: none;
}
.Criatura footer div .imaBoton {
  width: 100%;
  height: 100%;
  background: none;
  margin: 0;
  padding: 0;
}

.parpadeo {
  color: #f79bff;
  -webkit-animation: 0.7s parpadear infinite;
          animation: 0.7s parpadear infinite;
}

@-webkit-keyframes parpadear {
  70% {
    color: #3A5BA6;
  }
}

@keyframes parpadear {
  70% {
    color: #3A5BA6;
  }
}
.subidon {
  color: #a0ff9b;
  -webkit-animation: 0.7s subidon infinite;
          animation: 0.7s subidon infinite;
}

@-webkit-keyframes subidon {
  70% {
    font-size: 1.3em;
  }
}

@keyframes subidon {
  70% {
    font-size: 1.3em;
  }
}
.fijo {
  position: absolute;
  left: 100%;
  bottom: -40%;
}

.coin {
  height: 70%;
  margin-top: 0.3em;
}

